.empleo-recomendaciones-merere {
    font-family: Arial, sans-serif;
}

.header-merere {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 30px;
    margin-bottom: 20px;
    padding-top: 60px;
    box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
    flex-wrap: wrap;
}

.logo-merere {
    width: 140px;
    height: 54px;
    margin-bottom: -10px;
}

.nav-merere {
    display: flex;
    justify-content: center;
    flex-grow: 1;
    margin-left: 100px;
    flex-wrap: wrap;
}

.nav-link-merere {
    margin: 0 15px;
    padding: 10px;
    background: #fff;
    border: none;
    cursor: pointer;
    font-size: 16px;
    color: #333;
    transition: all 0.3s;
    position: relative;
}

.nav-link-merere.active-merere {
    color: #006497;
    font-weight: bold;
}

.nav-link-merere.active-merere::after {
    content: '';
    display: block;
    width: 100%;
    height: 2px;
    background: #006497;
    position: absolute;
    bottom: 0;
    left: 0;
}

.action-buttons-merere {
    display: flex;
    align-items: center;
}

.notify-button-merere {
    background-color: #fff;
    color: #006497;
    border: 1px solid #006497;
    margin-left: 10px;
    width: 206px;
    height: 44px;
    font-size: 16px;
}

.bell-button-merere {
    background-color: #fff;
    color: #006497;
    border: 1px solid #006497;
    margin-left: 10px;
    height: 44px;
}

.search-bar-merere {
    padding: 20px;
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 5px;
}

.tabs-merere {
    padding: 20px;
}

.tab-links-merere {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
    position: relative;
}

.tab-link-merere {
    margin-right: 20px;
    padding: 10px;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 16px;
    color: #5F5F5F;
    opacity: 0.7;
    transition: all 0.3s;
    display: flex;
    align-items: center;
    font-weight: bold;
    position: relative;
}

.tab-link-merere.active-merere {
    color: black;
    font-weight: bold;
    border-bottom: 3px solid #006497;
}

.tab-links-merere::before {
    content: '';
    position: absolute;
    width: 100%;
    height: 1px;
    background: #ddd;
    bottom: 0;
    left: 0;
}

.tab-content-merere {
    padding: 10px 0;
}

.ant-select-selector {
    border: none !important;
}

.icon-merere {
    font-size: 24px;
    color: #006497;
    padding-right: 10px;
}

/* Estilos para los inputs */
.search-input-merere:hover,
.select-input-merere:hover,
.city-input-merere:hover {
    background-color: #e0e0e0; /* Gris un poco ms oscuro */
    border: 2px solid #ccc !important; /* Borde ms grueso */
}

.search-input-merere:focus,
.select-input-merere:focus,
.city-input-merere:focus {
    background-color: #fff; /* Fondo original */
    border: 2px solid #81BFEC !important; /* Borde ms grueso y de color #81BFEC */
}

/* Estilos para el botn */
.search-button-merere:hover {
    background-color: #0078A8; /* Azul ligeramente ms claro */
    border-color: #81BFEC !important; /* Borde del color #81BFEC */
}

.search-button-merere:active {
    background-color: #006497; /* Vuelve al color original */
    box-shadow: 0 0 0 2px #81BFEC !important; /* Borde de color #81BFEC por fuera */
}

