    .city-input-merere {
        width: 170px;
    }

    .logo-merere {
        width: 140px;
        height: 54px;
        margin-bottom: -10px;
    }

    .header-merere {
        flex-direction: column;
        align-items: flex-start;
    }

    .nav-merere {
        margin-left: 0;
        margin-bottom: 10px;
        justify-content: flex-start;
    }

    .action-buttons-merere {
        flex-direction: column;
        align-items: flex-start;
    }

    .notify-button-merere {
        margin-left: 0;
        margin-top: 10px;
        width: 100%;
