/* Ocultar scrollbar y mantener funcionalidad de desplazamiento */
.custom-table .ant-table-body {
    scrollbar-width: none; /* Firefox */
    -ms-overflow-style: none; /* Internet Explorer 10+ */
    overflow-y: auto; /* Permitir desplazamiento vertical */
    height: 146px; /* Altura del rea desplazable */
   
  }
  
  .custom-table .ant-table-body::-webkit-scrollbar {
    display: none; /* Chrome, Safari y Opera */
  }
  
  /* Estilos personalizados para la tabla */
  .ant-table {
    border-collapse: separate; /* Asegurar separacin entre celdas */
  }
  
  .ant-table-tbody > tr > td {
    border-right: 1px solid #fff; /* Lnea blanca entre columnas */
    padding: 8px 16px; /* Padding personalizado para celdas */
  }
  
  .ant-table-tbody > tr > td:last-child {
    border-right: none; /* Eliminar la lnea derecha en la ltima columna */
  }
  
  .table-container {
    max-height: 400px; /* Puedes ajustar esto segn tus necesidades */
    overflow-y: auto; /* Permite el desplazamiento vertical */
    overflow-x: hidden; /* Oculta el desplazamiento horizontal */
    /* Estilo del fondo para el encabezado */
    .ant-table-thead > tr > th {
      background-color: #ffffff; /* Fondo blanco para el encabezado */
      font-weight: bold;
    }
  }
  .truncate {
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }
  
  