.principal-nav {
  font-family: Arial, sans-serif;
  padding-top: 150px; /* Ajusta el contenido para que no se superponga con el header fijo */
}
.principal-nav2 {
  font-family: Arial, sans-serif;
  padding-top: 140px; /* Ajusta el contenido para que no se superponga con el header fijo */
}

.principal-nav-header {
  position: fixed; /* Fija el header en la parte superior */
  top: 0; /* Alinea el header con el borde superior */
  left: 0; /* Alinea el header con el borde izquierdo */
  width: 100%; /* Asegrate de que ocupe todo el ancho de la pantalla */
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1px 30px;
  padding-top: 60px;
  margin-bottom: 20px; /* Este margen no es necesario porque el header es fijo */
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.1);
  flex-wrap: wrap;
  background-color: #fff; /* Aade un fondo para que no sea transparente */
  z-index: 1000; /* Asegura que el header est sobre otros elementos */
}



.principal-nav-logo {
  width: 140px;
  height: 54px;
  margin-bottom: -10px;
}

.principal-nav-nav {
  display: flex;
  justify-content: center;
  flex-grow: 1;
  margin-left: 100px;
  flex-wrap: wrap;
}

.principal-nav-link {
  margin: 0 15px;
  padding: 10px;
  background: #fff;
  border: none;
  cursor: pointer;
  font-size: 16px;
  color: #333;
  transition: all 0.3s;
  position: relative;
  text-decoration: none;
  font-family: Inter;
}

.principal-nav-link.active {
  color: #006497;
  font-weight: bold;
}

.principal-nav-link.active::after {
  content: '';
  display: block;
  width: 100%;
  height: 2px;
  background: #006497;
  position: absolute;
  bottom: 0;
  left: 0;
}

.principal-nav-action-buttons {
  display: flex;
  align-items: center;
}
